export const submitAction = ({ confirmation, setIsSaved, submit, setLocalEntryData, moveAll }) => {
    if (confirmation.name === 'submit') {
        if (moveAll) setLocalEntryData()
        submit()
        setIsSaved()

    }
    else {
        setIsSaved()
    }
}

export const message = ({ moduleEntry, complete }) => {
    if (moduleEntry?.jobPost) {
        let message = null
        const { areaDestinationName, areaName } = moduleEntry?.jobPost
        if (areaDestinationName) message = ` ${areaName} reefer ${areaDestinationName} - `
        else message = ` ${areaName} - `
        return message
    }
}

export const handleFailed = ({ setIsSaved, setConfirmation, action, setLocalEntryData }) => {
    setIsSaved()
    setConfirmation({ show: false, name: action })
}